<template>
  <div>
    <div class="Conference">회의화면 부분</div>
    <div class="ChatList"></div>
    <div class="ToolBox">툴 박스</div>
    <div class="ChatBox"></div>
    <hr />
    <div>
      <!-- 컨퍼런스 정보 관련 정보 표시(임시) -->
      <p>데이터 표시 확인</p>
      <p>
        conference name: {{ conferenceDetail.title }} / conference type: {{ categoryName }}
      </p>
    </div>
    <hr />
    <div>
      <!-- 방 종료 / 수정 관련 -->
      <!-- 방 개설자(주최자)만 표시되도록 추후 수정-->
      <conference-detail-update></conference-detail-update>
      <conference-detail-close></conference-detail-close>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import ConferenceDetailUpdate from './ConferenceDetailUpdate';
import ConferenceDetailClose from './ConferenceDetailClose';

const userStore = "userStore";
const conferenceStore = "conferenceStore";

export default {
  name: "Conference_detail",
  components: {
    ConferenceDetailUpdate,
    ConferenceDetailClose
  },
  data() {
    return {
      userName: "",
      message: "",
      recvList: [],
      // 카테고리 관련
      categoryName: "",
      // 회의 정보 관련
      conferenceId: this.$route.params.contentId,
    };
  },
  created() {
    // App.vue가 생성되면 소켓 연결을 시도합니다.
    ///////////////////////////////////////////////

    // 회의실 상세 데이터 가져오기
    // console.log('***')
    this.getConferenceInfo(this.conferenceId);

    // 카테고리 아이디와 일치하는 카테고리명 찾기
    this.matchCategory();
    
    // 방 참여 이력 생성
    this.createHistory(this.historyData());

    //방 참여 - 제대로 동작하지 않고(기존 participant null일때만 200 반환 & participant 변화 없음), 크게 의미가 있는 기능인지는 모르겠음
    // console.log('****************')
    // this.createParticipant(this.participantData());

  },
  watch: {
    conferenceDetail() {
      this.matchCategory();
    }, // conferenceDetail 항목이 변하면(수정되면) 카테고리명 다시 찾아서 렌더링하도록...
  },
  computed: {
    ...mapState(conferenceStore, ["conferenceDetail", "conferenceCategory"]),
    ...mapState(userStore, ["userInfo"]),
  },
  methods: {
    ...mapActions(conferenceStore, [
      "getConferenceInfo",
      "createHistory", 
      "createParticipant"
    ]),
    date_time() {
      let today = new Date();

      let year = today.getFullYear(); // 년도
      let month = today.getMonth() + 1; // 월
      let date = today.getDate(); // 날짜
      let hours = today.getHours(); // 시
      let minutes = today.getMinutes(); // 분
      let seconds = today.getSeconds(); // 초

      return (
        year +
        "-" +
        month +
        "-" +
        date +
        " " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds
      );
    },
    // 카테고리 아이디와 일치하는 카테고리명 찾기
    matchCategory() {
      // this.category = this.conferenceCategory.filter((conferenceCategory) => {
      //     return conferenceCategory.id === this.conferenceDetail.conference_type
      //   })
      for (const cc of this.conferenceCategory)
        if (cc.id === this.conferenceDetail.conference_type) {
          this.categoryName = cc.name;
        }
    },
    historyData() {
      return {
        conference_id: this.conferenceId,
        user_user_id: this.userInfo.user_id,
        // action: 1, // 뭔지 모르겠음 / null값 가능함 / 액션 기본값은 0
        // inserted_time: this.date_time() // 입력 시간 자동 생성이 안됨 / null 값 가능함// date_time()반영이 안됨-_-.....
      }
    },
    // participantData() {
    //   return {
    //     conferenceInfoDto: {
    //       id: this.conferenceId,
    //       user_user_id: this.userInfo.user_id,
    //     },
    //     userDto: {
    //       user_id: this.userInfo.user_id
    //     }
    //   }
    // }
  },
};
</script>

<style>
.Conference {
  border: solid;
  float: left;
  width: 70%;
  height: 800px;
}
.ChatList {
  border: solid;
  float: right;
  width: 30%;
  height: 800px;
}
.ToolBox {
  border: solid;
  float: left;
  width: 70%;
  height: 200px;
}
.ChatBox {
  border: solid;
  float: right;
  width: 30%;
  height: 200px;
}
</style>
